<template>
	<view class="boxs" @click="handleOrder">
		<view class="top">
			<view class="order-number">订单编号：{{order.OrderNo}}</view>
			<view class="status">{{statusList[order.Status]}}</view>
		</view>
		<view class="middle">
			<view class="title">{{order.PackageName}}</view>
			<view class="">
				<view class="name-money" v-for="(item,index) in order.ProductList">
					<view class="">{{item.ProductName}}</view>
					<view class="">{{item.UnitPrice}}</view>
				</view>
			</view>
			<view class="total">
				<text class="totle-name">合计</text>
				<text class="total-money">￥{{order.Price}}</text>
			</view>
		</view>
		<view class="bottom">
			<view class="cancel-order" @click.stop="cancelOrder" v-if="order.Status===0">取消订单</view>
			<view class="cancel-order" @click.stop="workOrder" v-else>查看劳作</view>
			<view class="pay-now" @click.stop="payNow" v-if="order.Status===0">立即付款</view>
			<view class="pay-now" @click.stop="handleAppraise" v-if="order.Status === 2">去评价</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			order: {
				default: {}
			}
		},
		data() {
			return {
				statusList: ['待付款', '待劳作', '待评价', '已完成']
			}
		},
		methods: {
			/**
			 * 取消订单
			 */
			async cancelOrder() {
				this.$emit('onCancelOrder', this.order.OrderID)
			},
			/**
			 * 查看劳作
			 */
			async workOrder() {
				this.$emit('onworkOrder', this.order.OrderID)
			},
			/**
			 * 立即支付
			 */
			payNow() {
				this.$emit('onPayNow', this.order.OrderNo)
			},
			/**
			 * 订单详情
			 */
			handleOrder() {
				if (this.order.Status === 0) {
					let items = JSON.stringify(this.order);
					uni.setStorageSync("workList", items);
					uni.navigateTo({
						url: '/pages/user-center/user-work/order-informatiom/index'
					})
				} else {

				}

			},
			/**
			 * 去评价
			 */
			handleAppraise() {
				uni.navigateTo({
					url: `/pages/user-center/user-work/appraise/index?type=0&orderNo=${this.order.OrderNo}&orderId=${this.order.OrderID}`
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.boxs {
		margin-top: 30rpx;
		background: #FFFFFF;
		box-shadow: 0px 2rpx 16rpx 0px rgba(0, 0, 0, 0.05);
		border-radius: 16rpx;
		padding: 20rpx;
	}

	.top {
		display: flex;
		justify-content: space-between;
		padding-bottom: 20rpx;
		border-bottom: 2rpx solid #F7F9FC;

		.order-number {
			font-size: 24rpx;
			font-weight: 400;
			color: #979DA6;
			line-height: 34rpx;
		}

		.status {
			font-size: 24rpx;
			font-weight: 400;
			color: #333333;
			line-height: 34rpx;
		}

	}

	.middle {
		border-bottom: 2rpx solid #F7F9FC;

		.title {
			font-size: 32rpx;
			font-weight: 500;
			color: #333333;
			line-height: 44rpx;
			padding: 20rpx 0;
		}

		.name-money {
			display: flex;
			justify-content: space-between;
			font-size: 28rpx;
			font-weight: 400;
			color: #999999;
			line-height: 40rpx;
			padding-bottom: 20rpx;
		}

		.total {
			display: flex;
			justify-content: space-between;
			padding-bottom: 20rpx;

			.total-name {
				font-size: 28rpx;
				font-weight: 500;
				color: #333333;
				line-height: 40rpx;
			}

			.total-money {
				font-size: 28rpx;
				font-weight: 500;
				color: #FEB042;
				line-height: 40rpx;
			}
		}
	}

	.bottom {
		padding-top: 20rpx;
		display: flex;
		justify-content: flex-end;
		text-align: center;
		line-height: 58rpx;

		.cancel-order {
			width: 152rpx;
			height: 58rpx;
			border-radius: 29rpx;
			border: 2rpx solid #999999;
			font-size: 24rpx;
			font-weight: 400;
			color: #999999;
			margin-right: 20rpx;
		}

		.pay-now {
			width: 152rpx;
			height: 58rpx;
			border-radius: 29rpx;
			border: 2rpx solid #00D08F;
			font-size: 24rpx;
			font-weight: 400;
			color: #00D08F;
			line-height: 58rpx;
		}
	}
</style>
